import axios from 'axios'


type User = {
  id: number;
  email: string;
  password: string;
  role: 'user' | 'admin';
};

export type Service = {
  id: number;
  name: string;
};

type Booking = {
  id: number;
  user: string;
  serviceId: number;
  dateTime: string;
};

const api = axios.create({
  baseURL: 'http://localhost:5000/api',
})

// Token automatisch anhÃ¤ngen
api.interceptors.request.use((config) => {
  const token = localStorage.getItem('token')
  if (token) {
    config.headers.Authorization = `Bearer ${token}`
  }
  return config
})

export default api

// Dummy-DB
export const services: Service[] = [
  { id: 1, name: 'Haarschnitt' },
  { id: 2, name: 'Bart trimmen' },
  { id: 3, name: 'Massage' },
];

// Lade aus LocalStorage falls vorhanden
if (typeof window !== 'undefined') {
  try {
    const storedServices = localStorage.getItem('services')
    if (storedServices) {
      const parsedServices = JSON.parse(storedServices)
      services.length = 0; // Leere das bestehende Array
      services.push(...parsedServices) // FÃ¼ge neue Services hinzu
    }
  } catch (error) {
    console.error('Fehler beim Laden der Services aus LocalStorage', error)
  }
}

// ðŸ” Lade gespeicherte Daten aus localStorage (einmalig beim Import)
let users: User[] = []
let bookings: Booking[] = []

if (typeof window !== 'undefined') {
  users = JSON.parse(localStorage.getItem('users') || '[]')
  bookings = JSON.parse(localStorage.getItem('bookings') || '[]')
}


let userIdCounter = users.length + 1
let bookingIdCounter = bookings.length + 1

// ðŸ” Helper: speichern
function saveUsers() {
  if (typeof window !== 'undefined') {
    localStorage.setItem('users', JSON.stringify(users))
  }
}

function saveBookings() {
  if (typeof window !== 'undefined') {
    localStorage.setItem('bookings', JSON.stringify(bookings))
  }
}


export function registerUser(email: string, password: string): { success: boolean; message: string } {
  const exists = users.find(user => user.email === email)
  if (exists) {
    return { success: false, message: 'User already exists' }
  }
  const role = email === 'MO.com' ? 'admin' : 'user'
  users.push({ id: userIdCounter++, email, password, role })
  saveUsers()
  return { success: true, message: 'User registered successfully' }
}

export function loginUser(email: string, password: string): { success: boolean; message: string } {
  const user = users.find(u => u.email === email && u.password === password)
  if (!user) {
    return { success: false, message: 'Invalid credentials' }
  }
  return { success: true, message: 'Login successful' }
}

export function getUserByEmail(email: string): User | undefined {
  return users.find(user => user.email === email)
}

export function createBooking(user: string, serviceId: number, dateTime: string): Booking {
  const booking = {
    id: bookingIdCounter++,
    user,
    serviceId,
    dateTime,
  }
  bookings.push(booking)
  saveBookings()
  return booking
}

export function getUserBookings(user: string): Booking[] {
  return bookings.filter((b) => b.user === user)
}

export function getAllBookings(): Booking[] {
  return bookings
}

export function deleteBooking(id: number): boolean {
  const index = bookings.findIndex(b => b.id === id)
  if (index !== -1) {
    bookings.splice(index, 1)
    saveBookings()
    return true
  }
  return false
}

function saveServices() {
  if (typeof window !== 'undefined') {
    localStorage.setItem('services', JSON.stringify(services))
  }
}

export function addService(name: string) {
  const newService = {
    id: services.length > 0 ? services[services.length - 1].id + 1 : 1,
    name,
  }
  services.push(newService)
  saveServices()
}

export function deleteService(id: number) {
  const index = services.findIndex(s => s.id === id)
  if (index !== -1) {
    services.splice(index, 1)
    saveServices()
    return true
  }
  return false
}

export function getServices(): Service[] {
  if (typeof window !== 'undefined') {
    const stored = localStorage.getItem('services')
    if (stored) {
      return JSON.parse(stored)
    }
  }
  return services // Falls nichts gespeichert, Standard-Services
}
